@import "../static/font/iconfont.css";
.ui-nc-header-warp {
  color: #e3e7ed;
}
.template-item-wrapper.fake-disabled input[disabled],
.template-item-wrapper.fake-disabled textarea[disabled],
.template-item-wrapper.fake-disabled .multiLangText-disabled .multi-lang-text {
  background: #fff !important;
  border-color: #d5d6d9 !important;
  cursor: auto;
}
.template-item-wrapper.fake-disabled input[disabled]:hover,
.template-item-wrapper.fake-disabled textarea[disabled]:hover,
.template-item-wrapper.fake-disabled .multiLangText-disabled .multi-lang-text:hover {
  border-color: #8cc6ff !important;
}
.template-item-wrapper.fake-disabled .u-form-control-wrapper .nc-select.u-select-disabled,
.template-item-wrapper.fake-disabled .u-form-control-wrapper .nc-select.u-select-disabled .u-select-selection {
  border-color: #d5d6d9 !important;
  background: #fff !important;
  cursor: pointer;
}
.template-item-wrapper.fake-disabled .u-form-control-wrapper .nc-select.u-select-disabled:hover,
.template-item-wrapper.fake-disabled .u-form-control-wrapper .nc-select.u-select-disabled .u-select-selection:hover {
  border-color: #8cc6ff !important;
}
.template-item-wrapper.fake-disabled label.u-checkbox {
  cursor: pointer;
}
.template-item-wrapper.fake-disabled label.u-checkbox.disabled .u-checkbox-label {
  opacity: 1;
  cursor: pointer;
}
.template-item-wrapper.fake-disabled label.u-checkbox input[type="checkbox"][disabled] {
  height: 14px;
  width: 14px;
  margin: 0;
  padding: 0;
}
.template-item-wrapper.fake-disabled label.u-checkbox.disabled .u-checkbox-label:before,
.template-item-wrapper.fake-disabled label.u-checkbox.disabled .u-checkbox-label:after {
  background: #fff !important;
  cursor: pointer;
}
.template-item-wrapper.fake-disabled label.u-checkbox.disabled.is-checked .u-checkbox-label:before,
.template-item-wrapper.fake-disabled label.u-checkbox.disabled.is-checked .u-checkbox-label:after {
  background: transparent !important;
}
.template-item-wrapper.fake-disabled span.u-switch:after {
  background: #fff;
  cursor: pointer;
}
.template-item-wrapper.fake-disabled span.u-switch.u-switch-disabled {
  background: #e7eff3;
  cursor: pointer;
}
.template-item-wrapper.fake-disabled span.u-switch.u-switch-disabled.is-checked {
  opacity: 1;
}
.template-item-wrapper.fake-disabled span.u-switch.is-checked {
  border-color: #cdf2e3;
  background: #cdf2e3;
}
.template-item-wrapper.fake-disabled span.u-switch.is-checked:after {
  background: #00cb7c;
}
.template-item-wrapper.fake-disabled span.u-switch.is-checked:before {
  color: #00c66e;
}
.template-item-wrapper.fake-disabled .refer-wrapper.refer-wrapper-disabled .refer.refer-input-disabled {
  background: #fff;
  border-color: #d5d6d9;
}
.template-item-wrapper.fake-disabled .refer-wrapper.refer-wrapper-disabled .refer.refer-input-disabled .icon-refer {
  cursor: pointer !important;
}
.template-item-wrapper.fake-disabled .refer-wrapper.refer-wrapper-disabled .refer.refer-input-disabled:hover {
  border-color: #8cc6ff;
}
.template-item-wrapper .checkbox-hotkey-wrapper {
  font-size: 14px;
  line-height: 14px;
}
.template-item-wrapper .checkbox-hotkey-wrapper .nc-checkbox.u-checkbox {
  height: 14px;
  min-width: 14px;
  margin: 0;
  display: block;
}
.template-item-wrapper .checkbox-hotkey-wrapper .nc-checkbox.u-checkbox .u-checkbox-label {
  display: block;
  min-width: 14px;
  height: 14px;
  padding: 0 0 0 14px;
  z-index: 0;
}
.template-item-wrapper .checkbox-hotkey-wrapper input[type="checkbox"] {
  width: 14px;
  height: 14px;
  margin: 0;
  padding: 0;
}
