.nc-breadcrumb.u-breadcrumb li {
  color: #717478;
  font-size: 12px;
  cursor: default;
}
.nc-breadcrumb.u-breadcrumb li a {
  color: #717478;
  font-size: 12px;
}
.nc-breadcrumb.u-breadcrumb li:last-child {
  color: rgba(0, 0, 0, 0.65);
}
.nc-breadcrumb.u-breadcrumb > li + li:before {
  content: "\E60E";
  font-family: 'iconfont';
  color: rgba(0, 0, 0, 0.45);
}
