.maxbox {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 231;
  width: 100vw;
  height: 100%;
  background-color: #fff;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 100% 100%;
  overflow: hidden;
  width: calc(100% - 32px);
  height: calc(100% - 16px);
  margin: 8px 16px;
}
