.u-loading-backdrop {
  z-index: 400 !important;
}
.nc-loading-show .u-loading-backdrop {
  z-index: 400 !important;
}
.nc-loading-show .u-loading-backdrop .u-loading.u-loading-rotate.nc-loading > div {
  display: block;
}
.nc-loading-hidden .u-loading-backdrop {
  z-index: 400 !important;
  background: none;
}
.nc-loading-hidden .u-loading-backdrop .u-loading.u-loading-rotate.nc-loading > div {
  display: none;
}
