.tree-hotkeys-wrapper {
  outline: none;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 100%;
  height: 100%;
  overflow: auto;
  vertical-align: top;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
}
.tree-hotkeys-wrapper .u-tree {
  outline: none;
}
.tree-hotkeys-wrapper > .u-tree-infinite-scroll,
.tree-hotkeys-wrapper > ul {
  width: calc(100% - 1px);
}
.nc-tree.u-tree {
  background-color: transparent;
}
.nc-tree.u-tree .u-tree-checkbox-inner {
  width: 14px;
  height: 14px;
  background-color: #fff;
}
.nc-tree.u-tree li span.u-tree-switcher.u-tree-roots_open:after,
.nc-tree.u-tree li span.u-tree-switcher.u-tree-center_open:after,
.nc-tree.u-tree li span.u-tree-switcher.u-tree-bottom_open:after,
.nc-tree.u-tree li span.u-tree-switcher.u-tree-noline_open:after {
  color: #757f8c;
}
.nc-tree.u-tree li span.u-tree-switcher.u-tree-roots_close:after,
.nc-tree.u-tree li span.u-tree-switcher.u-tree-center_close:after,
.nc-tree.u-tree li span.u-tree-switcher.u-tree-bottom_close:after,
.nc-tree.u-tree li span.u-tree-switcher.u-tree-noline_close:after {
  color: #757f8c;
}
.nc-tree.u-tree li a {
  color: #212121;
}
.nc-tree.u-tree li.u-tree-treenode-focused a:focus {
  outline: none;
}
.nc-tree.u-tree li.u-tree-treenode-focused a:focus .refer-tree-title {
  border-color: transparent;
}
.nc-tree.u-tree li a:hover {
  background-color: #e4e4e4;
  color: #e14c46;
}
.nc-tree.u-tree li a.u-tree-node-selected,
.nc-tree.u-tree li a.u-tree-node-selected.u-tree-node-content-wrapper,
.nc-tree.u-tree li a.u-tree-node-selected .u-tree-title {
  background-color: #e4e4e4;
  color: #e14c46;
}
.nc-tree.u-tree li span.u-tree-switcher + a.u-tree-node-content-wrapper {
  min-width: calc(100% - 23px);
  width: auto;
}
.nc-tree.u-tree li.u-tree-treenode-focused > a {
  background-color: #ebecf0;
}
.nc-tree.u-tree .node_right_opt {
  background-color: #e4e4e4;
}
.nc-tree.u-tree .u-tree-checkbox-checked + .u-tree-node-content-wrapper .title-middle {
  color: #e14c46;
}
.nc-tree.u-tree .u-tree-checkbox-checked .u-tree-checkbox-inner {
  background: #e14c46;
  border-color: #e14c46;
}
.nc-tree.u-tree .u-tree-checkbox-checked .u-tree-checkbox-inner:after {
  border: 2px solid #fff;
  border-top: 0;
  border-left: 0;
}
