/* FormGroup */
/*  Navlayout  */
.u-alert-dismissable,
.alert-dismissible {
  padding-right: 35px;
}
.u-alert {
  position: relative;
  padding: 10px;
  border: 1px solid transparent;
  border-radius: 4px;
  font-size: 12px;
}
.u-alert .alert-text {
  padding-left: 15px;
  padding-right: 15px;
}
.u-alert .close {
  position: absolute;
  right: 20px;
  top: 10px;
  font-size: 21px;
  font-weight: 700;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  opacity: 0.2;
  filter: alpha(opacity=20);
  cursor: pointer;
}
.u-alert-success.u-alert {
  background-color: #c8e6c9;
  border-color: #c8e6c9;
  color: #4caf50;
}
.u-alert-news.u-alert {
  background-color: #bbdefb;
  border-color: #bbdefb;
  color: #2196f3;
}
.u-alert-info.u-alert {
  background-color: #b2ebf2;
  border-color: #b2ebf2;
  color: #00bcd4;
}
.u-alert-warning.u-alert {
  background-color: #ffe0b2;
  border-color: #ffe0b2;
  color: #ff9800;
}
.u-alert-danger.u-alert {
  background-color: #ffcdd2;
  border-color: #ffcdd2;
  color: #f44336;
}
.dark.u-alert-success.u-alert {
  background-color: #4caf50;
  border-color: #4caf50;
  color: #ffffff;
}
.dark.u-alert-news.u-alert {
  background-color: #2196f3;
  border-color: #2196f3;
  color: #ffffff;
}
.dark.u-alert-info.u-alert {
  background-color: #00bcd4;
  border-color: #00bcd4;
  color: #ffffff;
}
.dark.u-alert-warning.u-alert {
  background-color: #ff9800;
  border-color: #ff9800;
  color: #ffffff;
}
.dark.u-alert-danger.u-alert {
  background-color: #f44336;
  border-color: #f44336;
  color: #ffffff;
}
.u-alert-dismissable .close,
.u-alert-dismissible .close {
  position: relative;
  top: -2px;
  right: -21px;
  color: inherit;
}
