.u-menu.nc-menu {
  color: rgba(0, 0, 0, 0.65);
  background: #fff;
  border: 1px solid #d9d9d9;
  border-right: 1px solid #e8e8e8;
}
.u-menu.nc-menu .u-menu {
  color: rgba(0, 0, 0, 0.65);
}
.u-menu.nc-menu .u-menu-inline,
.u-menu.nc-menu .u-menu-vertical {
  background: #fff;
}
.u-menu.nc-menu .u-menu-item-divider {
  background-color: #e8e8e8;
}
.u-menu.nc-menu:not(.u-menu-horizontal) .u-menu-item-selected {
  background-color: #fff;
}
.u-menu.nc-menu .u-menu-item-disabled,
.u-menu.nc-menu .u-menu-submenu-disabled {
  color: rgba(0, 0, 0, 0.25) !important;
}
.nc-menu .u-menu-item:hover,
.nc-menu .u-menu-item-active,
.nc-menu.u-menu:not(.u-menu-inline) .u-menu-submenu-open,
.nc-menu .u-menu-submenu-active,
.nc-menu .u-menu-submenu-title:hover {
  background-color: #f3f3f3;
}
.nc-menu.u-menu-root.u-menu-vertical {
  -webkit-box-shadow: 0px 2px 4px 2px rgba(0, 0, 0, 0.16);
          box-shadow: 0px 2px 4px 2px rgba(0, 0, 0, 0.16);
}
.nc-menu .u-menu-item-selected {
  color: rgba(0, 0, 0, 0.65);
}
