.u-checkbox {
  display: inline-block;
  margin-right: 24px;
  height: 16px;
  line-height: 16px;
  font-size: 12px;
  white-space: nowrap;
  cursor: pointer;
  outline: none;
  position: relative;
  line-height: 1;
  vertical-align: middle;
}
.u-checkbox input[type='checkbox'] {
  position: absolute;
  z-index: 3;
  cursor: pointer;
  opacity: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
  width: 100%;
  height: 100%;
}
.u-checkbox input[disabled] {
  cursor: not-allowed;
}
.u-checkbox input[type='checkbox']:focus + .u-checkbox-label:before {
  border-color: #f53c32;
}
.u-checkbox.is-checked .u-checkbox-label:before {
  -webkit-box-shadow: inset 0 0 0 10px #fff;
  box-shadow: inset 0 0 0 10px #fff;
  border-color: #a5adba;
}
.u-checkbox.is-checked .u-checkbox-label:after {
  color: #f53c32;
  content: "\e658";
  line-height: 16px;
  font-size: 12px;
  font-weight: 600;
}
.u-checkbox.is-checked.u-checkbox-inverse .u-checkbox-label:before {
