.ea-tip-wapper {
  position: relative;
  width: 100%;
  height: 100%;
}
.ea-tip-wapper .ea-tip {
  text-align: center;
  position: absolute;
  left: 50%;
  top: 45%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.ea-tip-wapper .ea-tip .ea-tip-img {
  display: inline-block;
  width: 120px;
  height: 120px;
  background: url(./tip.png) 0 0 no-repeat;
  background-size: 100%;
}
.ea-tip-wapper .ea-tip .ea-tip-desc {
  font-size: 14px;
  margin-top: 16px;
  margin-bottom: 12px;
  color: #888;
}
.ea-tip-hidden {
  display: none;
}
