.user-unselect {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.pointer-events-none td div,
.pointer-events-none td span {
  pointer-events: none;
}
.mouse-selectable-shape {
  width: 0px;
  height: 0px;
  position: absolute;
  z-index: 99999;
  display: block;
  background-color: rgba(50, 128, 252, 0.2);
}
